.App {
  text-align: center;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  background-color: #20639b;
  color: whitesmoke;
  padding: 1%;
  height: 4vh;
  font-size: 25px;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif

}

.HeaderItem {
  background-color: whitesmoke;
  flex: 1;
}

.footer {
  display: flex;
  flex-direction: row;
  background-color: #20639b;
  padding-top: 30px; 
  height: 9vh;
}

.HomeBody {
    display: flex;
    flex-direction: row;
    height: 100%;
}
.ItemBody {
    height: 85vh;
    width: 100%;
}
.About h1{
    font-size: 90px;
    padding: 0px;
}

.About {
  background-color: #3caea3;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 10px;
  overflow: scroll;
  overflow: hidden;
  height: 100%;
}

.Skills {
  background-color: #f6d55c;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 10px;
  overflow: scroll;
  overflow: hidden;
  height: 100%;
}

.Projects {
  background-color: #ed553b;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 10px;
  overflow: scroll;
  overflow: hidden;
  height: 100%;
}






.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
